import 'ScssComponents/_contact_popup.scss';
import 'ScssComponents/_contact_form.scss';
import initPopup from './initPopup';

export default class ContactPopup {
	/* change height when form container is full */
	changeHeight() {
		const $popup = document.querySelector('.contactPopup');
		const $content = document.querySelector('.contactPopupContent');
		const padding = 20 * 2;
		if (!$popup || !$content) return;
		if ($content.clientHeight > $popup.clientHeight - padding) {
			$content.classList.add('contact_popup__in--height_mod');
		} else {
			$content.classList.remove('contact_popup__in--height_mod');
		}
	}

	init() {
		initPopup(
			'.contactOpenPopup',
			'.contactPopup',
			'.contactPopupContent',
			'.contactClosePopup',
		);
		this.changeHeight();
	}
}
